import { Container, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import { faPlusSquare } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default function SimpleAccordion() {
  return (
    <Container maxWidth="md" sx={{ mt: 2 }}>
      <Accordion sx={{ backgroundColor: '#edede7'}}>
        <AccordionSummary
          expandIcon={<FontAwesomeIcon icon={faPlusSquare} />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>Qu'est-ce qu'une agence de communication thique ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Une agence de communication thique s'engage  promouvoir des pratiques de communication responsables, respectueuses de l'environnement, des droits humains et des principes thiques dans l'ensemble de ses activits et projets.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion sx={{ backgroundColor: '#edede7'}}>
        <AccordionSummary
          expandIcon={<FontAwesomeIcon icon={faPlusSquare} />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography>Quels sont les accompagnements que propose Hora ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Hora propose une gamme d'accompagnements allant de la stratgie de communication, la cration de contenu, la gestion des rseaux sociaux, la conception graphique, jusqu' la formation en communication thique et durable.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion sx={{ backgroundColor: '#edede7'}}>
        <AccordionSummary
          expandIcon={<FontAwesomeIcon icon={faPlusSquare} />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography>Quelle diffrence entre le greenwashing et la communication thique ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Le greenwashing est une stratgie de marketing qui vise  donner une image cologique fausse ou exagre  une entreprise ou  un produit. En revanche, la communication thique est base sur l'honntet, la transparence et le respect de principes thiques, cologiques et sociaux dans toutes les actions de communication.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion sx={{ backgroundColor: '#edede7'}}>
        <AccordionSummary
          expandIcon={<FontAwesomeIcon icon={faPlusSquare} />}
          aria-controls="panel4a-content"
          id="panel4a-header"
        >
          <Typography>Qui sommes-nous ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Nous sommes Hora, une agence de communication thique ddie  aider les entreprises  communiquer de manire responsable et durable. Notre quipe est compose de professionnels expriments qui partagent une passion pour la communication thique et l'innovation.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion sx={{ backgroundColor: '#edede7'}}>
        <AccordionSummary
          expandIcon={<FontAwesomeIcon icon={faPlusSquare} />}
          aria-controls="panel5a-content"
          id="panel5a-header"
        >
          <Typography>Pourquoi le nom Hora ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Le nom Hora est inspir de la desse grecque des saisons et du temps qui passe. Cela reprsente notre engagement envers une communication durable qui respecte les cycles naturels et les besoins changeants de notre socit.
          </Typography>
        </AccordionDetails>
      </Accordion>
    </Container>
  );
}
